@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&display=swap);


header {
  display: flex;
  position: fixed;
  margin-left: 0;
  background: transparent;
  height: 64px;
  min-height: 50px;
  justify-content: space-between;
  align-items: center;
  top: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
  z-index: 1;
}

.Menu {
  position: fixed;
  top: 0px; /* Adjust the top position as needed */
  left: 20px; /* Adjust the left position as needed */
  z-index: 1;
  display: flex;
  flex-direction: column;
  background: var(--color-8);
  align-items: flex-start;
  max-height: 90vh; /* prevent items from going off the screen */
  overflow-y: auto; /* enable scrolling if items exceed max-height */
  margin-top: auto;
  border-radius: 8px;
}

.hamburger {
  position: fixed;
  top: 10px; 
  left: 10px;
  color: #ffffff;
}

.menuItem {
  border-radius: 13px;
  color: #ffffff;
  font-size: 17px;
  font-weight: bold;
  cursor: pointer;
  display: block;
  padding: 8px 32px;
  text-decoration: none;
  background-color: var(--color-7);
}

.menuItem:hover {
  background-color: var(--color-6);
  border-radius: 8px;
  color: #ffffff;
  
}


@media screen and (min-width: 600px) {
  .Menu {
    flex-direction: row;
    align-items: start;
    max-height: 90vh;
    overflow-y: auto;
    margin-top: auto;
    padding: 10px; /* Adjust padding as needed */
  }

  .Menu .menuItem {
    margin-top: 0;
    margin-right: 15px;
    padding: 8px 32px;
    border-radius: 8px;
    color: #fff;
    background-color: var(--color-8); /* Set the background color to match UserInfo */
    font-size: 17px;
    font-weight: bold;
    cursor: pointer;
    text-decoration: none;
  }

  .Menu .menuItem:hover {
    background-color: var(--color-7);
  }



  .hamburger {
    position: relative;
    top: auto;
    left: auto;
  }


}

.wallet-status a {
