box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.modal-content p {
  /* Styles for the text before the colon */
  color: #333;
  font-weight: bold;
   margin: 5px 0;
